<template>
  <div>
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <!-- <el-tab-pane label="活动设置" name="1" @click="$router.push({path:'/Createactivity/settings'})">
          <router-view></router-view>
      </el-tab-pane>
      <el-tab-pane label="奖品设置" name="2">
        <router-view></router-view>
      </el-tab-pane> -->
      <el-tab-pane label="抽奖池设置" name="1">
        <router-view></router-view>
      </el-tab-pane>
	  <el-tab-pane label="中奖名单" name="2">
	    <router-view></router-view>
	  </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  export default{
    data() {
      return {
        activeName: '1'
      }
    },

    created() {
      if(this.activeName=='1'){
        this.$router.push({name:'Createactivity/Lotterypool'})
      }
    },
    methods: {
      handleClick(tab, event) {
        // console.log(tab);
        switch(tab.name){
          case "1":
            this.$router.push({name:'Createactivity/Lotterypool'})
          break;
          case "2":
            this.$router.push({name:'Createactivity/Winninglist'})
          break;
        }
      }
    }
  }
</script>

<style>
</style>



